import GoodsItem from './GoodsItem.vue'
export default {
	name: 'GoodsList',
	props: {
		goods: {
			type: Object,
			default () {
				return {}
			}
		}
	},
	computed: {
		list() {
			if (this.type === 'pop') {
				return this.pops
			} else if (this.type === 'new') {
				return this.new
			} else {
				return this.sell
			}
		}
	},
	data() {
		return {
			flowList: [],
			pops: {},
			new: {},
			sell: {},
			type: ''
		}
	},
	methods: {
		itemClick(id) {
			uni.navigateTo({
				url: '/pages/detail/Detail?id=' + id
			})
		},
		init(type, data) {
			this.type = type
			if (type === 'pop') {
				this.pops = data
			} else if (type === 'new') {
				this.new = data
			} else {
				this.sell = data
			}
		}
	}
}
